<template>
  <div class="list">
    <h3>Movie List</h3>
    <div class="search">
      <input type="text" v-model="search" placeholder="제목을 입력하세요."/>
      <div class="icon"></div>
      <!-- <select v-on:change="inputSelectVal">
        <option value="rank" selected @click="aa">인기순</option>
        <option value="name">이름순</option>
      </select> -->
    </div>
    
    <article>
        <item v-for="item in filteredList" :key="item.rank" :num="item.id" ></item>
    </article>
    <router-link to="/add" class="add">영화추가</router-link>
  </div>
</template>
<script>
import item from '../components/Item.vue'

export default {
  name: 'list-vue',
  data(){
    return {
      num : 0,
      search: '',
    }
  },
  components : {
    item
  },
  mounted(){
    this.$store.state.items.sort(function(a,b){
      return a.id - b.id
    })
  },
  computed: {
    filteredList() {
      return this.$store.state.items.filter(item => {
        return item.movieName.toLowerCase().includes(this.search.toLowerCase()) ;
        
      })      
    },
  },
  methods:{
    inputSelectVal: function (e) {
        const sel = e.currentTarget;
        const filterData = [...this.$store.state.items];
        switch(sel.value) {
            case 'rank' : //인기순 정렬
              filterData.sort(function(a,b){
                return a.rank - b.rank
              })
              this.$store.state.items = filterData
              console.log('인기순')
              console.log(filterData)
              console.log(this.$store.state.items)
              
              break;
            // case 'name' : //이름순 정렬
            //   filterData.sort(function(a,b){
            //     return a.movieName.localeCompare(b.movieName)
            //   })
            //   this.$store.state.items = filterData
            //   console.log('이름순')
            //   console.log(filterData)
            //   break;            
        }
    },
  }
}
</script>
